<template>
    <div>
        <v-app-bar id="appBar" app dark :elevation="showScrollToTopFab?4:0"
                   :color="showScrollToTopFab?'primary':'transparent'">
            <v-app-bar-nav-icon @click="$router.back()">
                <v-icon>mdi-arrow-left</v-icon>
            </v-app-bar-nav-icon>
            <v-toolbar-title>{{showScrollToTopFab?article.title:null}}</v-toolbar-title>
            <v-spacer/>
            <v-menu v-if="$vuetify.breakpoint.smAndDown" origin="top center" offset-overflow absolute
                    transition="scale-transition">
                <template v-slot:activator="{ on }">
                    <v-btn icon dark v-on="on" @click="generateQrCode">
                        <v-icon>mdi-cellphone-link</v-icon>
                    </v-btn>
                </template>
                <div style="width: 150px;height: 150px">
                    <VueQr :margin="10" :text="qrLinkText" style="width: 100%;height: 100%"/>
                </div>
            </v-menu>
            <v-tooltip v-else bottom>
                <template v-slot:activator="{ on }">
                    <div v-on="on">
                        <v-menu origin="top center" offset-overflow absolute transition="scale-transition">
                            <template v-slot:activator="{ on }">
                                <v-btn icon dark v-on="on" @click="generateQrCode">
                                    <v-icon>mdi-cellphone-link</v-icon>
                                </v-btn>
                            </template>
                            <div style="width: 150px;height: 150px">
                                <VueQr :margin="10" :text="qrLinkText" style="width: 100%;height: 100%"/>
                            </div>
                        </v-menu>
                    </div>
                </template>
                <span>在其他设备上继续阅读</span>
            </v-tooltip>
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn v-on="on" icon @click="drawer = !drawer">
                        <v-icon v-if="drawer">mdi-close</v-icon>
                        <v-icon v-else>mdi-table-of-contents</v-icon>
                    </v-btn>
                </template>
                <span>{{drawer?"关闭":"目录"}}</span>
            </v-tooltip>
        </v-app-bar>
        <v-navigation-drawer app v-model="drawer" right :expand-on-hover="!$vuetify.breakpoint.smAndDown"
                             :permanent="drawer && !$vuetify.breakpoint.smAndDown"
                             :temporary="$vuetify.breakpoint.smAndDown">
            <v-treeview return-object dense :items="catalogue" activatable transition
                        @update:active="updateActive" hoverable/>
        </v-navigation-drawer>
        <v-img :src="article.imgUrl" style="height: 400px; margin-top: -70px">
            <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center" style="background-color: #1976d2">
                    <v-progress-circular indeterminate color="grey lighten-5"/>
                </v-row>
            </template>
        </v-img>
        <v-content>
            <v-card-title class="display-1 justify-center align-center white--text"
                          style="height: 300px;width: 100%;margin-top: -470px;font-weight: bold">
                {{article.title}}
            </v-card-title>
        </v-content>
        <v-card id="card" :tile="$vuetify.breakpoint.smAndDown"
                :raised="!$vuetify.breakpoint.smAndDown"
                :flat="$vuetify.breakpoint.smAndDown" class="mx-auto"
                max-width="1000px"
                style="margin-top: -180px">
            <v-card-text>{{article.content}}</v-card-text>
            <div id="likeFab" class="d-block text-center pt-5">
                <v-tooltip bottom>
                    <template v-slot:activator="{ on }">
                        <v-btn fab color="accent" v-on="on" @click="showLikeQrCode">
                            <v-icon>mdi-thumb-up-outline</v-icon>
                        </v-btn>
                    </template>
                    <span>赞赏</span>
                </v-tooltip>
            </div>
            <v-card-text class="d-inline-block text-end">{{article.timeString}}</v-card-text>
            <v-divider/>
            <div class="d-flex">
                <v-icon class="pa-2">mdi-view-list</v-icon>
                <v-chip-group column>
                    <v-chip v-for="type in types" :key="type" :href="'/index/type/'+type">{{type}}</v-chip>
                </v-chip-group>
            </div>
            <v-divider/>
            <div class="d-flex">
                <v-icon class="pa-2">mdi-tag-multiple</v-icon>
                <v-chip-group column>
                    <v-chip v-for="tag in tags" :key="tag" :href="'/index/tag/'+tag">{{tag}}</v-chip>
                </v-chip-group>
            </div>
            <v-divider/>
        </v-card>
