                        "\n" +
                        "海客谈瀛洲，烟涛微茫信难求；\n" +
                        "越人语天姥，云霞明灭或可睹。\n" +
                        "天姥连天向天横，势拔五岳掩赤城。\n" +
                        "天台四万八千丈，对此欲倒东南倾。\n" +
                        "\n" +
                        "我欲因之梦吴越，一夜飞度镜湖月。\n" +
                        "湖月照我影，送我至剡溪。\n" +
                        "谢公宿处今尚在，渌水荡漾清猿啼。\n" +
                        "脚著谢公屐，身登青云梯。\n" +
                        "半壁见海日，空中闻天鸡。\n" +
                        "千岩万转路不定，迷花倚石忽已暝。\n" +
