            generateQrCode: function () {
            },
            showLikeQrCode: function () {
                console.log("showLikeQrCode")
            },
            updateActive(array) {
                console.log(array)
            },
            handleScroll() {
                //滚动条偏移量
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                //文章开始偏移量
                let cardOffsetTop = document.getElementById("card").offsetTop
                //文章结束偏移量
                let likeFabOffsetTop = document.getElementById("likeFab").offsetTop
                //是否显示滑倒顶部的FAB
                this.showScrollToTopFab = scrollTop >= cardOffsetTop
                // console.log("scrollTop", scrollTop, "cardOffsetTop", cardOffsetTop, "likeFabOffsetTop", likeFabOffsetTop, "totalHeight", this.totalHeight, "window.screen.height", window.screen.availHeight)
                //计算实际阅读百分比
                if (scrollTop >= cardOffsetTop) {
                    if (scrollTop + cardOffsetTop <= likeFabOffsetTop - cardOffsetTop) {
                        this.percentOfRead = ((scrollTop - cardOffsetTop) / (likeFabOffsetTop - cardOffsetTop - cardOffsetTop - cardOffsetTop)).toFixed(2)
                    } else {
                        this.percentOfRead = 1.0
                    }
                } else {
                    this.percentOfRead = 0.0
                }
                //计算网页百分比
                this.realPercentOfRead = (scrollTop) / this.totalHeight
                //TODO 修改项目根目录
                this.qrLinkText = "http://192.168.1.110:8081#" + this.$route.path.toString() + "?realPercentOfRead=" + this.realPercentOfRead
                // console.log("this.percentOfRead", this.percentOfRead, "this.realPercentOfRead", this.realPercentOfRead)
            }
        },
        created: function () {
            this.getArticle()
        },
        mounted: function () {
            this.qrLinkText = "http://192.168.1.110:8081#" + this.$route.path.toString()
            this.totalHeight = document.body.scrollHeight
            console.log("this.totalHeight", this.totalHeight)
            //监听滚动事件，然后用handleScroll这个方法进行相应的处理
            window.addEventListener('scroll', this.handleScroll)
            let realPercentOfRead = this.$route.query.realPercentOfRead
            if (realPercentOfRead !== undefined && realPercentOfRead !== 0.0) {
                this.$vuetify.goTo(realPercentOfRead * this.totalHeight)
                this.snackbar = true
            }
        },
        watch: {},
        destroyed() {
            //移除滚动事件，然后用handleScroll这个方法进行相应的处理
            window.removeEventListener('scroll', this.handleScroll, false)
        }
